<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
        <div class="new-modal liquidity-deposit-modal" @click.stop="">
                <button class="new-modal-back-button thin" @click="closeModal"></button>
            <div class="modal-content">
        <p class="window-title">{{ titleOverride || $localizeFilter('withdraw_funds_from_liquidity_balance') }}</p>

        <p class="window-text">{{$localizeFilter('withdraw_from_balance_modal_text_amount', ['USDT'])}}</p>
        <div class="input-field">
            <input id="amount" class="input-class" :placeholder="$localizeFilter('amount_in', ['USDT'])" type="text" v-model.trim="amount"  :class="{ invalid: v$.amount.$error }"/>
            <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                {{ error.$message }}
            </small>
        </div>
        
        <div class="modal-warning">
            <img class="warning-label" src="../../assets/newImages/warning-label.svg" alt="">
            <p v-if="withdrawToWallet===false" class="window-text warning">{{$localizeFilter('withdraw_from_balance_modal_warning')}}</p>
            <p v-else class="window-text warning">{{$localizeFilter('withdraw_from_balance_modal_warning_alternative')}}</p>
        </div>

    </div>
    <button class="basic-button submit-button" :class="submitInProgress ? 'inactive' : ''" @click="submitHandler">{{$localizeFilter('withdraw_funds')}}</button>

    </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { helpers, required, numeric, minValue, maxValue, minLength, maxLength } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'

function multiplyNumberByDecimals(value, decimalsNumber) {
    // const decimals = decimalsNumber.toString().length
    let [integer, fraction] = value.toString().split('.')
    // console.log(decimalsNumber)
    let res = 0n;
    if(fraction) {
        res += BigInt(fraction) * decimalsNumber;
        for( let i = 0; i < fraction.length; i++ ) {
            res /= BigInt(10);
        }
    }
    res += BigInt(integer) * decimalsNumber;
    return res;
}

export default defineComponent({
    props: {
        withdrawToWallet: {
            type: Boolean,
            required: true
        },
        titleOverride: {
            type: String,
            default: null
        },
        fromGoalsBalance: {
            type: Boolean,
            default: false
        },
    },
    validations() {
        return {
            amount: {
                // required: helpers.withMessage(({ 
                //     $pending,
                //     $invalid,
                //     $params,
                //     $model
                // })=>`${$pending} ${$invalid} ${$params} ${$model}`, required),
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), required),
                minValue: helpers.withMessage(localizeFilter('min_value', [this.minAmount]), minValue(this.minAmount))
            },
        };
    },
    setup() {
        return { v$: useVuelidate() };
    },
    data: () => ({
        amount: "",
        minAmount: 0,
        submitInProgress: false,
    }),
    methods: {
        closeModal(reload) {
            this.$emit("closeModal", reload);
        },
        async submitHandler() {
            this.v$.$touch();
            if (this.v$.$error /*|| !this.conditionsAccepted*/) {
                return;
            }

            this.submitInProgress = true

            // send data to store
            try {
             //   let amount = multiplyNumberByDecimals(this.amount, this.assetDecimalsMap[this.pool.leftCoinTicker]).toString();
                const data = {amount: this.amount, toWallet: this.withdrawToWallet, fromGoalsBalance: this.fromGoalsBalance}
                await this.$store.dispatch('withdrawLiquidityProvidingBalance', data)
                this.$message(localizeFilter('withdrawing_funds_successful'))
                this.$emit('closeModal')
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } catch(e) {
                this.$error(e)
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } 
        },
    },
    computed: {
    }
})
</script>


