<template>
    <div class="modal-layout">
        <div class="darken" @click="closeModal()">
            <div class="modal-window"
                :class="(type == 'welcomeBonus' ? 'welcome-bonus' : (type == 'socialBoom' ? 'social-boom' : (type == 'operation' ? 'operation' : ''))) + (step > 1 ? 'animated' : '')"
                @click.stop="">

                <button class="modal-back-button new-modal-back-button" @click="closeModal"></button>

                <div class="modal-container">
                    <div class="modal-content">
                        <p class="window-title">{{ $localizeFilter('withdrawal') }}</p>

                        <p class="window-text">{{ $localizeFilter('modal_withdrawal_text_1_account', [coin]) }}</p>
                        <div class="input-field">
                            <input id="amount" class="input-class" :placeholder="$localizeFilter('amount_in', [coin])"
                                type="text" v-model.trim="amount" :class="{ invalid: v$.amount.$error }" />
                            <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                                {{ $filters.capitalizeFirstLetter(error.$property) }}
                                {{ error.$message }}
                            </small>
                        </div>

                        <!-- <Toggle style="margin-top: 10px;" :selected="selectedMethod" :title="$localizeFilter('withdrawal_method')+':'" :variants="methods" @toggle="toggle" /> -->

                        <p class="window-text">{{ $localizeFilter('modal_withdrawal_text_2') }}</p>
                        <div class="input-field">
                            <input id="address" class="input-class wallet"
                                :placeholder="$localizeFilter('wallet_number', [selectedMethod])" type="text"
                                v-model.trim="address" :class="{ invalid: v$.address.$error }"
                                v-maska="'TXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX'" />
                            <small class="error" v-for="(error, index) of v$.address.$errors" :key="index">
                                {{ $filters.capitalizeFirstLetter(error.$property) }}
                                {{ error.$message }}
                            </small>
                        </div>

                        <div class="modal-warning">
                            <img class="warning-label" src="../../assets/newImages/warning-label.svg" alt="">
                            <p class="window-text warning">{{ $localizeFilter('liquidity_modal_withdrawal_warning',
                                [minAmount.toFixed(0), coin, minUSDAmount.toFixed(0)]) }}</p>
                        </div>

                        <!-- <div class="conditions-block" @click="acceptСonditions">
                        <input type="checkbox" id="checkbox" class="checkbox" v-model="conditionsAccepted" @click="acceptСonditions">
                        <div class="checkbox-labels-block">
                            <label for="checkbox" class="conditions-label">{{$localizeFilter('modal_withdrawal_agreement')}}</label>
                            <label class="conditions-hint">{{$localizeFilter('withdrawal_checkbox_hint')}}</label>
                        </div>
                    </div> -->

                        <label class="error" v-if="notEnoughFunds">{{ $localizeFilter('not_enough_funds_on_balance_1',
                            [coin]) }}</label>

                        <!-- :class="conditionsAccepted ? '' : 'inactive'" -->
                    </div>
                    <button class="basic-button basic-button submit-button" :class="submitInProgress ? 'inactive' : ''"
                        @click="submitHandler">{{ $localizeFilter('next') }}</button>

                </div>

            </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { required, numeric, minValue, minLength, maxLength } from "@vuelidate/validators";
import Toggle from "@/components/Toggle.vue"
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    props: {
        fromProviding: {
            type: Boolean,
            required: true
        },
        fromGoalsBalance: {
            type: Boolean,
            required: true
        }
    },
    components: {
        Toggle
    },
    data: () => ({
        amount: "",
        address: "",
        coin: 'USDT',
        selectedMethod: 'TRON',  // 'BTC','USDT'
        notEnoughFunds: false,
        minAmount: 0,
        minUSDAmount: 0,
        submitInProgress: false,
        // conditionsAccepted: false
    }),
    setup() {
        return { v$: useVuelidate() };
    },
    validations() {
        return {
            amount: { required, numeric, minValue: minValue(this.minAmount) },
            address: { required, minLength: minLength(34), maxLength: maxLength(34) }
        };
    },
    methods: {
        closeModal(reload) {
            this.$emit("closeModal", reload);
        },
        async submitHandler() {
            this.v$.$touch();
            if (this.v$.$error /*|| !this.conditionsAccepted*/) {
                return;
            }
            this.submitInProgress = true
            try {
                // let method = JSON.parse(JSON.stringify(this.selectedMethod))
                // if (method=='USDT') method='USDT.TRC20'
                const formData = { amount: this.amount, withdrawWalletAddress: this.address, withdrawMethod: this.coin + '.TRC20', currencyCode: this.coin, fromProviding: this.fromProviding, fromGoalsBalance: this.fromGoalsBalance };
                const { withdrawBalanceRequestId, fee } = await this.$store.dispatch('createWithdrawBalanceRequest', formData)
                // this.$message(localizeFilter('withdraw_successfull'))
                // this.$emit('closeModal', false)
                this.$emit('showWithdrawalConfirmation', withdrawBalanceRequestId, fee)
                setTimeout(() => {
                    this.submitInProgress = false
                }, 1000)
            } catch (e) {
                if (e.message == 'Not enough funds') {
                    this.notEnoughFunds = true
                } else {
                    this.notEnoughFunds = false
                    this.$error(e)
                }
                setTimeout(() => {
                    this.submitInProgress = false
                }, 1000)
            }
        },
        async loadData() {
            const data = { coinTicker: this.coin }
            try {
                const { minAmount, minUSDAmount } = await this.$store.dispatch('getWithdrawCredentials', data)
                // this.address = data.withdrawWalletAddress
                // this.selectedMethod = (data.withdrawMethod=='USDT.TRC20'?'USDT':'BTC')
                this.minAmount = minAmount
                this.minUSDAmount = minUSDAmount
                this.$forceUpdate()
            } catch (e) {
                this.$error(e)
            }
        }
        // acceptСonditions() {
        //     this.conditionsAccepted = !this.conditionsAccepted
        // },
        // toggle(index) {
        //     this.selectedMethod = this.methods[index]
        // },    
    },
    async mounted() {
        this.loadData()
    }
})
</script>

